import React from 'react';
import styled from 'styled-components';
import {Button} from './Button';
import {Menu} from './Menu';
import defaultSettings from '../defaultSettings';

interface Props {
    onClose: () => void;
    settings: typeof defaultSettings;
}

const Info = ({onClose, settings}: Props) =>
    <Menu title="Welcome to Towers of Hanoi - Speedrun Edition.">
        <p>This is an online version of <Link href="https://en.wikipedia.org/wiki/Tower_of_Hanoi">Towers of
            Hanoi</Link>, where the controls are optimized to allow you to complete the game as fast as possible.
            Your goal is to move all disks to the far right peg.
            You can only move one disk at a time, and you cannot place a larger disk onto a smaller one.</p>
        <p>The are two ways to control this game.</p>
        <h2>Fast (2 keystrokes per move)</h2>
        <p>Use the number keys to pickup/place disks at the corresponding peg number.</p>
        <h2>Super Fast (1 keystroke per move)</h2>
        <ul>
            <li><strong>{settings.keyBind21}</strong> - move top disk from peg 2 to peg 1</li>
            <li><strong>{settings.keyBind12}</strong> - move top disk from peg 1 to peg 2</li>
            <li><strong>{settings.keyBind13}</strong> - move top disk from peg 1 to peg 3</li>
            <li><strong>{settings.keyBind31}</strong> - move top disk from peg 3 to peg 1</li>
            <li><strong>{settings.keyBind32}</strong> - move top disk from peg 3 to peg 2</li>
            <li><strong>{settings.keyBind23}</strong> - move top disk from peg 2 to peg 3</li>
        </ul>
        <p><strong>Note</strong>: Super Fast controls only work for 3 peg games.
            These key binds can also be customized in the settings.</p>
        <CenterButton onClick={onClose}>Close</CenterButton>
        Made with ‚ô• by <Link href="https://doteye.online">.i</Link>
    </Menu>;



const Link = styled.a.attrs(() => ({
    target: '_blank',
}))`
    color: lightskyblue;
`;

const CenterButton = styled(Button)`
    position: relative;
    left: 50%;
    transform: translateX(-50%);
`;

export default Info;
