import styled from 'styled-components';

type DiskProps = {
    $width: number;
    $height: number;
    $color: number;
    $holding?: boolean;
};

const Disk = styled.div.attrs<DiskProps>(({$color, $width, $height}) => ({
    // If these were set normally in the CSS below, over 200 classes
    // would be generated for each color/width/height combination.
    style: {
        backgroundColor: `hsl(${$color * 360}, 100%, 50%)`,
        width: `${$width}px`,
        height: `${$height}px`,
    }
}))<DiskProps>`
    border-radius: 10px;
    margin: ${p => p.$holding ? '20px' : 'auto'} auto auto auto;
`;

export default Disk;
