import React from 'react';
import defaultSettings from '../defaultSettings';
import styled from 'styled-components';

type SliderProps = {
    settings: typeof defaultSettings;
    setSettings: (settings: typeof defaultSettings) => void;
    settingsKey: 'disks' | 'stacks';
    min: number;
    max: number;
    disabled: boolean;
    resetGame: (settings?: typeof defaultSettings) => void;
};

const LongInput = styled.input`
    width: 300px;
`;

const SettingsSlider = ({settings, setSettings, settingsKey, min, max, disabled, resetGame}: SliderProps) =>
    <LongInput
        type="range"
        min={min}
        max={max}
        value={settings[settingsKey]}
        disabled={disabled}
        onChange={event => {
            const newSettings = {...settings, [settingsKey]: parseInt(event.target.value)};
            setSettings(newSettings);
            resetGame(newSettings);
        }}
    />;

export default SettingsSlider;