import React, {useCallback, useEffect, useRef, useState} from 'react';
import Disk from './Disk';
import Stack from './Stack';
import defaultSettings from '../defaultSettings';
import Settings from './Settings';
import styled from 'styled-components';
import compareObjects from "../util/compareObjects";
import {NavBar} from './NavBar';
import optimalMoves from '../util/optimalMoves';
import {Win} from './Win';
import {setHighScore} from '../util/highScore';
import Info from './Info';

const Game = () => {
    const [stacks, setStacks] = useState([
        Array.from({length: defaultSettings.disks}, (_, i) => i + 1),
        [],
        [],
    ]);

    const [holding, setHolding] = useState<{width: number, from: number} | null>(null);
    const [moves, setMoves] = useState<{from: number, to: number}[]>([]);
    const [startTime, setStartTime] = useState<number | null>(null);
    const [endTime, setEndTime] = useState<number | null>(null);
    const [settings, setSettings] = useState(defaultSettings);
    const [settingsShown, setSettingsShown] = useState(false);
    const [infoShown, setInfoShown] = useState(true);
    const [, rerender] = useState(0);

    const stacksRef = useRef<HTMLDivElement>(null);

    const checkForWin = () => {
        if (
            stacks[stacks.length - 1].length === settings.disks &&
            compareObjects(stacks[stacks.length - 1], Array.from({length: settings.disks}, (_, i) => i + 1))
        ) {
            const now = Date.now();
            setHighScore(settings, now - startTime!);
            setEndTime(now);
        }
    };

    const onKeyPress = (event: KeyboardEvent) => {
        if (event.key === settings.keyReset) resetGame();
        if (endTime) return;

        if (event.key === settings.keyUndo) {
            undo();
            return;
        }

        const newStacks = [...stacks];
        const move = (from: number, to: number) => {
            if (from >= settings.stacks || to >= settings.stacks) return;
            if (!settings.illegalMoves && stacks[to][0] < newStacks[from][0]) return;
            const disk = newStacks[from].shift();
            if (!disk) return;
            if (moves.length === 0) setStartTime(Date.now());
            setMoves(moves.concat([{from, to}]));
            newStacks[to].unshift(disk);
            setStacks(newStacks);
            checkForWin();
        }
        if (!holding) {
            if (event.key === settings.keyBind21) move(1, 0);
            else if (event.key === settings.keyBind12) move(0, 1);
            else if (event.key === settings.keyBind13) move(0, 2);
            else if (event.key === settings.keyBind31) move(2, 0);
            else if (event.key === settings.keyBind32) move(2, 1);
            else if (event.key === settings.keyBind23) move(1, 2);
        }
        const numberKey = Number(event.key) - 1;
        if (isNaN(numberKey)) return;
        if (numberKey < 0 || numberKey >= stacks.length) return;
        if (holding) {
            if (!settings.illegalMoves && stacks[numberKey][0] < holding.width) return;

            const newStacks = [...stacks];
            newStacks[numberKey].unshift(holding.width);
            setStacks(newStacks);
            setHolding(null);

            if (holding.from !== numberKey) setMoves(moves.concat([{from: holding.from, to: numberKey}]));
            checkForWin();
        } else {
            setHolding(stacks[numberKey][0] ? {
                width: stacks[numberKey][0],
                from: numberKey,
            } : null);
            if (moves.length === 0) setStartTime(Date.now());

            const newStacks = [...stacks];
            newStacks[numberKey].shift();
            setStacks(newStacks);
        }

        // Don't allow multiple moves per render.
        document.removeEventListener('keypress', onKeyPress);
    };

    const resetGame = useCallback((newSettings: typeof defaultSettings = settings) => {
        let stacks = [];
        for (let i = 0; i < newSettings.stacks; ++i) stacks.push([]);
        stacks[0] = Array.from({length: newSettings.disks}, (_, i) => i + 1);
        setStacks(stacks);

        setHolding(null);
        setStartTime(null);
        setEndTime(null);
        setMoves([]);
    }, [settings]);

    const resetSettings = useCallback((newSettings: typeof defaultSettings = defaultSettings) => {
        setSettings(newSettings);
        resetGame(newSettings);
    }, [resetGame]);

    const getWidth = (size: number) => {
        if (!stacksRef.current) return 0;
        const maxWidth = (stacksRef.current.firstChild as HTMLDivElement).offsetWidth;
        return settings.disks * 50 > maxWidth ? size / settings.disks * maxWidth : size * 50;
    };

    const getColor = (size: number) => {
        return size / settings.disks;
    };

    const undo = useCallback(() => {
        const newStacks = [...stacks];

        if (holding) {
            newStacks[holding.from].unshift(holding.width);
            setHolding(null);
        } else {
            if (moves.length === 0) return;
            const newMoves = [...moves];
            const lastMove = newMoves.splice(newMoves.length - 1, 1)[0];

            newStacks[lastMove.from].unshift(stacks[lastMove.to][0]);
            newStacks[lastMove.to].shift();

            setMoves(newMoves);
        }

        setStacks(newStacks);
    }, [stacks, holding, moves]);

    const resize = () => rerender(x => x + 1);

    useEffect(() => {
        document.addEventListener('keypress', onKeyPress);
        window.addEventListener('resize', resize);
        return () => {
            document.removeEventListener('keypress', onKeyPress);
            window.removeEventListener('resize', resize);
        }
    });

    // Resize once after initial page load and when settings change.
    useEffect(resize, [settings]);

    // Load settings on initial page load.
    useEffect(() => {
        const rawSettings = window.localStorage.getItem('settings');
        if (rawSettings) {
            const newSettings = JSON.parse(rawSettings);
            setInfoShown(false);
            setSettings(newSettings);
            resetGame(newSettings);
        }
    }, [resetGame]);

    // Save settings on change.
    useEffect(() => {
        window.localStorage.setItem('settings', JSON.stringify(settings));
    }, [settings]);

    const stackWidth = 100 / settings.stacks;
    const diskHeight = Math.min((window.innerHeight - 150) / settings.disks, 50);

    return (
        <>
            <NavBar
                startTime={startTime}
                endTime={endTime}
                numMoves={moves.length}
                optimalMoves={optimalMoves(settings)}
                settings={settings}
                undo={undo}
                reset={resetGame}
                toggleSettings={() => setSettingsShown(!settingsShown)}
                toggleInfo={() => setInfoShown(!infoShown)} />
            {infoShown && <Info onClose={() => setInfoShown(false)} settings={settings} />}
            {settingsShown && <Settings
                settings={settings}
                setSettings={setSettings}
                resetSettings={resetSettings}
                resetGame={resetGame}
                moves={moves}
                endTime={endTime} />}
            {startTime && endTime && <Win moves={moves} settings={settings} startTime={startTime} endTime={endTime} />}

            <div style={{visibility: settings.blindfold ? 'hidden': 'unset'}}>
                {holding && <Disk $width={getWidth(holding.width)} $height={diskHeight} $color={getColor(holding.width)} $holding />}
                <Stacks ref={stacksRef}>
                    {stacks.map((stack, key) =>
                        <Stack
                            key={key}
                            width={stackWidth}
                            showPeg={settings.showPegs}
                        >
                            {stack.map((index, key) => <Disk key={key} $width={getWidth(index)} $height={diskHeight} $color={getColor(index)} />)}
                        </Stack>
                    )}
                </Stacks>
            </div>
        </>
    );
};

export default Game;

const Stacks = styled.div`
    position: fixed;
    bottom: 0;
    width: 100%;
    pointer-events: none;
    z-index: -1;
`;
