import {useEffect, useState} from 'react';

const Timer = ({startTime, endTime}: {startTime: number | null, endTime: number | null}) => {
    const [dummy, setDummy] = useState(0);

    useEffect(() => {
        if (endTime) return;

        // Force a re-render every 10 milliseconds.
        const interval = setInterval(() => setDummy(dummy + 1), 10);

        return () => clearInterval(interval);
    });

    if (startTime) {
        const remainingTime = ((endTime ?? Date.now()) - startTime) / 1000;
        return <>{remainingTime.toFixed(3)} seconds</>;
    }

    return <>Not Started</>;
};

export default Timer;
