import styled from 'styled-components';
import React from 'react';
import optimalMoves from '../util/optimalMoves';
import defaultSettings from '../defaultSettings';
import {getHighScore} from '../util/highScore';

interface Props {
    moves: {from: number, to: number}[];
    settings: typeof defaultSettings;
    endTime: number;
    startTime: number;
}

export function Win({moves, settings, endTime, startTime}: Props) {
    return (
        <>
            <Text>
                <Emoji>üèÜ</Emoji>
                <h2>Game complete!</h2>
            </Text>
            <NoBulletList>
                {moves.length === optimalMoves(settings) && <li>You had the optimal solution!</li>}
                <li><strong>Average moves per second</strong>: {(1000 * moves.length / (endTime - startTime)).toFixed(2)}</li>
                <li><strong>Average optimal moves per second</strong>: {(1000 * optimalMoves(settings) / (endTime - startTime)).toFixed(2)}</li>
                <li><strong>Your Best Time</strong>: {(getHighScore(settings) / 1000).toFixed(3)} seconds</li>
            </NoBulletList>
        </>
    )
}

const Emoji = styled.span`
    font-size: 48px;
`;

const Text = styled.div`
    display: flex;
`;

const NoBulletList = styled.ul`
    list-style: none;
    padding-left: 15px;
    margin-top: 5px;
`;
