import defaultSettings from "../defaultSettings";

const optimalMoves = (settings: typeof defaultSettings) => {
    if (settings.stacks < 3 && settings.disks > 1) return Infinity;
    if (settings.illegalMoves) return settings.disks * 2 - 1;

    let moves = 1;
    let moveIncrement = 2;
    for (let i = 1; i < settings.disks; ++i) {
        moves += moveIncrement;
        if (i % (settings.stacks - 2) === 0) moveIncrement *= 2;
    }

    return moves;
};

export default optimalMoves;
