import defaultSettings from '../defaultSettings';

function makeKey(settings: typeof defaultSettings) {
    return `HighScore/${settings.disks}/${settings.stacks}/${settings.illegalMoves}/${settings.blindfold}`;
}

export function getHighScore(settings: typeof defaultSettings) {
    const score = localStorage.getItem(makeKey(settings));
    return score ? parseInt(score) : Infinity;
}

export function setHighScore(settings: typeof defaultSettings, score: number) {
    if (getHighScore(settings) > score) localStorage.setItem(makeKey(settings), score.toString());
}
