import styled from 'styled-components';
import icon from '../assets/icon.png';
import Timer from './Timer';
import React from 'react';
import {Button} from './Button';
import defaultSettings from '../defaultSettings';

interface Props {
    startTime: number | null;
    endTime: number | null;
    numMoves: number;
    optimalMoves: number;
    settings: typeof defaultSettings;
    undo: () => void;
    reset: () => void;
    toggleSettings: () => void;
    toggleInfo: () => void;
}

export function NavBar({startTime, endTime, numMoves, optimalMoves, settings, undo, reset, toggleSettings, toggleInfo}: Props) {
    return (
        <Wrapper>
            <img src={icon} alt="Logo" width="50px" />
            <h1>Towers of Hanoi - Speedrun Edition</h1>
            <Controls>
                <Info>
                    <Timer startTime={startTime} endTime={endTime} /><br />
                    Moves: {numMoves}/{optimalMoves === Infinity ? '‚àû' : optimalMoves} Optimal
                </Info>
                <Button onClick={undo}>Undo&nbsp;({settings.keyUndo})</Button>
                <Button onClick={() => reset()}>Reset&nbsp;({settings.keyReset})</Button>
                <Button onClick={toggleSettings}>Settings</Button>
                <Button onClick={toggleInfo}>?</Button>
            </Controls>
        </Wrapper>
    )
}

const Wrapper = styled.nav`
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: black;
    padding: 10px;
    gap: 10px;
    
    h1 {
        margin: 0;
    }
`;

const Controls = styled.div`
    display: flex;
    gap: 5px;
    align-items: center;
`;

const Info = styled.div`
    width: 225px;
`;